@import url(https://use.typekit.net/gpk2rpd.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,body{margin:0;padding:0}#root{width:100%;height:100vh;display:flex;flex-direction:column;align-items:center;background-color:#161616}.App{height:100%;min-height:100vh;width:80%;display:flex;flex-direction:column}.image{height:55vh;width:100%;background-image:url(/static/react/media/curtains_pink.26f6c5b1.jpg);background-repeat:no-repeat;background-size:100%;background-position-x:center;background-position-y:top;margin-top:15vh}.identifier{height:18%;width:46.8%;display:flex;flex-direction:column;justify-content:center;position:absolute;margin-top:68vh;padding-left:12px;background-color:#F3EBF6}.btn{height:10%;width:30%;position:absolute;margin-top:70vh;right:10%;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:#AC8FB6}.btn img{width:25px}.name,.status,.isStatus{font-family:tenon,sans-serif;font-style:normal}.name{font-weight:800;font-size:20pt;color:#161616}.status{font-weight:200;font-size:10pt;color:#AC8FB6;margin-top:22px}.isStatus{font-weight:800;font-size:16pt;color:#AC8FB6}

